<template>
    <v-container fluid>
        <v-timeline class="hidden-sm-and-down">
            <div class="py-5 display-2">
                Experience
            </div>

            <v-timeline-item
                v-for="(entry, index) in experience"
                :key="index"
                color="teal lighten-2"
                fill-dot
            >
                <entry-preview
                    :entry="entry"
                    v-on:launch-full-entry="launch_full_entry"
                ></entry-preview>
                <template v-slot:opposite>
                    <div class="title teal--text">
                        {{ entry.dates }}
                    </div>
                </template>
            </v-timeline-item>
        </v-timeline>

        <v-timeline dense reverse class="hidden-md-and-up">
            <div class="py-5 display-2">
                Experience
            </div>

            <v-timeline-item
                v-for="(entry, index) in experience"
                :key="index"
                color="teal lighten-2"
                fill-dot
            >
                <entry-preview
                    :entry="entry"
                    v-on:launch-full-entry="launch_full_entry"
                ></entry-preview>
            </v-timeline-item>
        </v-timeline>

        <full-entry
            v-if="active_entry"
            :show="show_full_entry"
            :entry="active_entry"
            v-on:close-full-entry="show_full_entry = false"
        ></full-entry>
    </v-container>
</template>

<script>
import experience from '@/assets/templates/experience/experience_data.json'
import entry_preview from './components/entry_preview'
import full_entry from './components/full_entry'


export default {
    components: {
        "entry-preview": entry_preview,
        "full-entry": full_entry
    },
    data: function() {
        return({
            experience: experience,
            active_entry: null,
            show_full_entry: false
        })
    },
    methods: {
        launch_full_entry: function(id) {
            // Update active entry
            this.active_entry = this.experience.filter((entry) => {
                return entry.id === id
            })[0]
            // Activate visibility.
            this.show_full_entry = true
        }
    }
}
</script>
