<template>
    <v-dialog v-model="show_dialog" fullscreen>
        <v-card>
            <v-toolbar class="black" dark>
                <v-icon @click="$emit('close-full-entry')">clear</v-icon>
            </v-toolbar>
            <v-card-text class="black--text">
                <!-- Position title. -->
                <div class="display-2 pt-5 pb-3">
                    {{ entry.position_name }}
                </div>
                <!-- Relevant data. -->
                <div>
                    <!-- Employer details. -->
                    <p>
                        <v-icon>location_city</v-icon> {{ entry.employer }}
                    </p>
                    <!-- Location details. -->
                    <p>
                        <v-icon>location_on</v-icon> {{ entry.location }}
                    </p>
                    <!-- Time employed. -->
                    <p>
                        <v-icon>date_range</v-icon> {{ entry.dates }}
                    </p>
                </div>
                <!-- Description. -->
                <p
                    v-for="(paragraph, index) in entry.description"
                    :key="index"
                    class="title font-weight-light"
                >
                    {{ paragraph }}
                </p>
            </v-card-text>
        </v-card>
    </v-dialog>
</template>

<script>
export default {
    props: ["entry", "show"],
    computed: {
        show_dialog: {
            get: function() {
                return this.show
            },
            set: function() {
                this.$emit("close-full-entry")
            }
        }
    },
    data: function() {
        return({

        })
    }
}
</script>

