<template>
    <v-card class="elevation-2 pa-3" color="white">
        <!-- Render Title and subtitle -->
        <v-card-title primary-title>
            <v-layout row class="px-3">
                <v-avatar v-if="image" class="py-3">
                    <v-img :src="image"></v-img>
                </v-avatar>
                <div v-if="image" class="pl-3"></div>
                <div>
                    <!-- Position title. -->
                    <div class="headline">
                        {{ entry.position_name }}
                    </div>
                    <!-- Mobile employement dates view. -->
                    <div class="subtitle-2 hidden-md-and-up py-3">
                        <v-layout column align-start>
                            <v-icon>date_range</v-icon>
                            <div class="pl-1">
                                {{ entry.dates }}
                            </div>
                        </v-layout>
                    </div>
                    <!-- Mobile employer view. -->
                    <div class="subtitle-2 pb-1 hidden-md-and-up">
                        <v-layout column align-start>
                            <v-icon>location_city</v-icon>
                            <div class="pl-1">
                                {{ entry.employer }}
                            </div>
                        </v-layout>
                    </div>
                    <!-- Desktop employer view. -->
                    <div class="subtitle-2 pb-1 pl-3 hidden-sm-and-down">
                        <v-layout row>
                            <v-icon>location_city</v-icon>
                            <div class="pl-1">
                                {{ entry.employer }}
                            </div>
                        </v-layout>
                    </div>
                </div>
            </v-layout>
        </v-card-title>
        <!-- Render Paragraphs -->
        <v-card-text class="black--text">
            <div>
                {{ entry.summary }}
            </div>
        </v-card-text>

        <v-card-actions class="px-3">
            <v-btn
                class="teal lighten-2 white--text"
                @click="$emit('launch-full-entry', entry.id)"
            >
                Learn More
            </v-btn>
        </v-card-actions>
    </v-card>
</template>

<script>

export default {
    props: ["entry"],
    data: function() {
        return({
            image: null
        })
    },
    mounted: function() {
        if(this.entry.image) {
            this.image = require("@/assets/images/" + this.entry.image)
        }
    }
}
</script>

